(function() {
  'use strict';

  angular
    .module('webProject', ['ngAnimate',
                            'ngCookies',
                            'ngTouch',
                            'ngSanitize',
                            'ngMessages',
                            'ngAria',
                            'ngResource',
                            'ui.router',
                            'ui.bootstrap',
                            'angular-redactor',
                            'ui.bootstrap.datetimepicker',
                            'angularMoment',
                            'ksSwiper'
                            ])

})();

(function() {
  'use strict';

  angular
    .module('webProject')
    .directive('match', match);

  /** @ngInject */
  function match() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/match/matchlist/matchlist.html',
      controller: MatchController,
      controllerAs: 'MM'
    };

    return directive;

    /** @ngInject */
    function MatchController(send, $log, moment) {
      var vm = this;

      vm.matches = [];

      vm.calculateDiff = function(givenTime){
        return moment(givenTime).fromNow(true)
      }

      send.request('/match/list', 'GET').then(function(res){
        vm.matches = res;
      }, function(err){
        $log.debug(err);
      })
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('webProject')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($log, send, $state) {
    var vm = this;
    vm.user = {};

    vm.login = function(){
      send.request('/auth/local/login', 'POST', vm.user)
        .then(
          function(res){
            $log.debug('/auth/local/login POST', res.message);
            $state.go('admin')
          },
          function(err){
            $log.debug('error', err);
          });
    }
  }
})();

