(function(){
  'use strict';

  angular
    .module('webProject')
    .controller('SignUpController', SignUpController);

    /** @ngInject */
    function SignUpController (send, $log){
      var vm = this;
      vm.user = {}
      vm.differentPassword = false;
      vm.success = false;

      vm.check = function() {
        if (vm.user.confirmPassword && vm.user.password && vm.user.password === vm.user.confirmPassword){
          vm.differentPassword = false;
          vm.register();
        }
        else {
          vm.differentPassword = true;
        }
      }

      vm.register = function(){
        send.request('/auth/local/', 'POST', vm.user)
          .then(function(res){
            $log.debug('/api/auth/local POST',res);
            vm.success = true;
            vm.user = {};
          }, function(err){
            $log.debug('/api/auth/local POST',err);
            vm.error = true;
          })
      }
    }

})()

(function() {
  'use strict';

  angular
    .module('webProject')
    .directive('swiper', swiper);

  /** @ngInject */
  function swiper() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/swiper/swiper.html',
      controller: SwiperController,
      controllerAs: 'SS'
    };

    return directive;
    /** @ngInject */
    function SwiperController(send, $log) {
      var vm = this;
      vm.posts = [];

      send.request('/post/top', 'GET')
        .then(function(res) {
          vm.posts = res;
        }, function(err) {
          $log.debug(err);
        })
    }
  }

})();

